, 0.12)}\",\n    21: \"0px 8px 40px 7px #{rgba($color, 0.12)}\",\n    22: \"0px 8px 42px 7px #{rgba($color, 0.12)}\",\n    23: \"0px 9px 44px 8px #{rgba($color, 0.12)}\",\n    24: \"0px 9px 46px 8px #{rgba($color, 0.12)}\"\n  );\n}\n","$small-spinner-size: 1.2em;\n$medium-spinner-size: 1.8em;\n$large-spinner-size: 2.1em;\n\nnovo-spinner {\n  display: inline-block;\n  width: $medium-spinner-size;\n  height: $medium-spinner-size;\n  position: relative;\n\n  &.size-small {\n    width: $small-spinner-size;\n    height: $small-spinner-size;\n  }\n\n  &.size-large {\n    width: $large-spinner-size;\n    height: $large-spinner-size;\n  }\n\n  .dot {\n    width: 100%;\n    height: 100%;\n    position: absolute;\n    left: 0;\n    top: 0;\n  }\n\n  .dot:before {\n    content: \"\";\n    display: block;\n    margin: 0 auto;\n    width: 15%;\n    height: 15%;\n    background-color: #333;\n    border-radius: 100%;\n    -webkit-animation: dotFadeDelay 1.2s infinite ease-in-out both;\n    animation: dotFadeDelay 1.2s infinite ease-in-out both;\n  }\n\n  @each $analytics, $color in $analytics-colors {\n    &.#{$analytics} {\n      .dot:before {\n        background-color: $color;\n      }\n    }\n  }\n\n  &.multicolor {\n    @each $name, $color in $analytics-colors {\n      $i: index($analytics-colors, $name $color);\n      .dot:nth-of-type(#{$i}) {\n        &:before {\n          background-color: $color;\n        }\n      }\n    }\n  }\n\n  &.white,\n  &[inverse] {\n    .dot:before {\n      background-color: $white;\n    }\n  }\n\n  .dot2 {\n    -webkit-transform: rotate(30deg);\n    -ms-transform: rotate(30deg);\n    transform: rotate(30deg);\n  }\n  .dot3 {\n    -webkit-transform: rotate(60deg);\n    -ms-transform: rotate(60deg);\n    transform: rotate(60deg);\n  }\n  .dot4 {\n    -webkit-transform: rotate(90deg);\n    -ms-transform: rotate(90deg);\n    transform: rotate(90deg);\n  }\n  .dot5 {\n    -webkit-transform: rotate(120deg);\n    -ms-transform: rotate(120deg);\n    transform: rotate(120deg);\n  }\n  .dot6 {\n    -webkit-transform: rotate(150deg);\n    -ms-transform: rotate(150deg);\n    transform: rotate(150deg);\n  }\n  .dot7 {\n    -webkit-transform: rotate(180deg);\n    -ms-transform: rotate(180deg);\n    transform: rotate(180deg);\n  }\n  .dot8 {\n    -webkit-transform: rotate(210deg);\n    -ms-transform: rotate(210deg);\n    transform: rotate(210deg);\n  }\n  .dot9 {\n    -webkit-transform: rotate(240deg);\n    -ms-transform: rotate(240deg);\n    transform: rotate(240deg);\n  }\n  .dot10 {\n    -webkit-transform: rotate(270deg);\n    -ms-transform: rotate(270deg);\n    transform: rotate(270deg);\n  }\n  .dot11 {\n    -webkit-transform: rotate(300deg);\n    -ms-transform: rotate(300deg);\n    transform: rotate(300deg);\n  }\n  .dot12 {\n    -webkit-transform: rotate(330deg);\n    -ms-transform: rotate(330deg);\n    transform: rotate(330deg);\n  }\n  .dot2:before {\n    -webkit-animation-delay: -1.1s;\n    animation-delay: -1.1s;\n  }\n  .dot3:before {\n    -webkit-animation-delay: -1s;\n    animation-delay: -1s;\n  }\n  .dot4:before {\n    -webkit-animation-delay: -0.9s;\n    animation-delay: -0.9s;\n  }\n  .dot5:before {\n    -webkit-animation-delay: -0.8s;\n    animation-delay: -0.8s;\n  }\n  .dot6:before {\n    -webkit-animation-delay: -0.7s;\n    animation-delay: -0.7s;\n  }\n  .dot7:before {\n    -webkit-animation-delay: -0.6s;\n    animation-delay: -0.6s;\n  }\n  .dot8:before {\n    -webkit-animation-delay: -0.5s;\n    animation-delay: -0.5s;\n  }\n  .dot9:before {\n    -webkit-animation-delay: -0.4s;\n    animation-delay: -0.4s;\n  }\n  .dot10:before {\n    -webkit-animation-delay: -0.3s;\n    animation-delay: -0.3s;\n  }\n  .dot11:before {\n    -webkit-animation-delay: -0.2s;\n    animation-delay: -0.2s;\n  }\n  .dot12:before {\n    -webkit-animation-delay: -0.1s;\n    animation-delay: -0.1s;\n  }\n\n  @-webkit-keyframes dotFadeDelay {\n    0%,\n    39%,\n    100% {\n      opacity: 0;\n    }\n    40% {\n      opacity: 